.slider
  overflow: hidden
  @media screen and (min-width: 900px)
    display: block
  .image-slider
    width: 100%
    img
      width: 100%
      object-fit: cover
      object-position: top
      height: 400px
      @media screen and (min-width: 900px)
        height: 400px
      @media screen and (min-width: 1200px)
        height: 500px
      @media screen and (min-width: 1600px)
        height: 798px
    .container
      display: flex
      justify-content: center