*
  -webkit-tap-highlight-color: transparent
  box-sizing: border-box

html
  scroll-behavior: smooth

body
  margin: 0
  padding: 0
  font-family: monospace
  height: 100vh
  display: flex
  flex-direction: column

.wrapper
  max-width: 1350px
  padding: 0 20px
  position: relative
  margin: 0 auto

h1, h2, h3
  text-transform: uppercase

a
  text-decoration: none
  &:hover, &:visited, &:link, &:active
    color: inherit

//a[href="#"]
//  pointer-events: none

.pointer
  cursor: pointer
  
.block
  display: block!important
  
.hide
  display: none!important

.flex
  display: flex!important

/* Custom Logo */
.custom-logo
  width: 150px
  height: 150px

/* Page Up Button */
.page-up
  position: fixed
  display: flex
  justify-content: center
  align-items: center
  padding: 10px 15px
  opacity: 0
  bottom: 40px
  right: 40px
  background: black
  transition: .3s ease-in-out
  border-radius: 0 0 10px 0
  cursor: pointer
  z-index: 1
  border: 1px solid white
  img
    width: 15px
    height: auto
  &.active
    opacity: 1

/* IE Check
/* ----------------------------------------------- */

.ie-check
  position: absolute
  top: 0
  left: 0
  right: 0
  display: none
  margin-bottom: 30px
  padding: 20px 15px
  text-align: center
  font-size: 26px
  color: #fff
  z-index: 9999999999
  width: 100%
  -webkit-box-sizing: border-box
  -moz-box-sizing: border-box
  box-sizing: border-box
  a
    display: block
    font-size: 16px
